import { Avatar } from '@material-ui/core'
import SidebarOption from './SidebarOption'
import styles from './sidebar.module.css'
import HeadsetIcon from '@material-ui/icons/Headset';
import SearchIcon from '@material-ui/icons/Search';
import FavoriteIcon from '@material-ui/icons/Favorite';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
const Sidebar = () => {
    return (
        <div className={styles.sidebar}>
        <div className={styles.div1}>
        <Avatar src="data:image/png;base64,"/>
        <h5>Bob Smith</h5>
        </div>

        <div className={styles.div2}>
        
        <SidebarOption Icon={HeadsetIcon} Title="Discover"/>
        <SidebarOption Icon={SearchIcon} Title="Search"/>
        <SidebarOption Icon={FavoriteIcon} Title="Favorite"/>
        <SidebarOption Icon={PlayCircleOutlineIcon} Title="Playlists"/>
        <SidebarOption Icon={InsertChartIcon}Title="Charts"/>
        
    
     
        
        </div>
            
        </div>
    )
}

export default Sidebar
