/* eslint-disable jsx-a11y/heading-has-content */
import React from 'react'
import styles from './Footer.module.css'
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ShuffleIcon from '@material-ui/icons/Shuffle';
import RepeatIcon from '@material-ui/icons/Repeat';
import VolumeDownIcon from '@material-ui/icons/VolumeDown';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import PlayCircleFilledWhiteIcon from '@material-ui/icons/PlayCircleFilledWhite';
const Footer = () => {
    return (
        <div className={styles.footer}>
          <div className={styles.footer1}>
          <img src="data:image/jpeg;base64," alt="hello"/>
          <h5>song Name</h5>
          </div>  
          <div className={styles.footer2}>
        
          <SkipPreviousIcon className={styles.icon}/>
          <PlayCircleFilledWhiteIcon className={styles.play}/>
          <SkipNextIcon className={styles.icon}/>
         
          
          <h3 className={styles.fancy}></h3>
          
          </div>
          <div className={styles.footer3}>
         
          <FavoriteBorderIcon className={styles.icon}/>
          <ShuffleIcon className={styles.icon}/>
          <RepeatIcon className={styles.icon}/>
          <VolumeDownIcon className={styles.icon}/>
          </div>
        </div>
    )
}

export default Footer
